const downloadPdf = (ab, fileName) =>{
    var blob = new Blob([ab], {type: "application/pdf"});
    FileSaver.saveAs(blob, fileName);
}

class IndexPage extends React.Component{
 
    constructor(){
        super()
        this.state= {
            invoices:null,
            crrInvoice:null,
            apiKey:null,
            adminId: null,
            crrRow: null,
            loading:true,
            numOfPages:null
        }
        this.setActive= this.setActive.bind(this)
        this.clearActive = this.clearActive.bind(this)
        this.onDownloadMain = this.onDownloadMain.bind(this)
        this.onDownloadRow = this.onDownloadRow.bind(this)
        this.fetchPdf = this.fetchPdf.bind(this)
        this.onDocumentLoadSuccess = this.onDocumentLoadSuccess.bind(this)
    }

    async fetchPdf(pdfId){
         const {apiKey, adminId} = this.state;
        //  const apiKey = "73d4a6832af65b7ab18ce9a99d13e4010c040e396ab3b7aaf8538302eb31d612"
        //  const adminId = "285050283149494256"
         
         const res = await fetch(
             `https://moneybirdhandlerproxy.azurewebsites.net/api/MoneyBirdProxy`,
             {
                 mode:"cors",
                 method:"POST",
                 body: JSON.stringify({
                     method:"GET",
                     path:`${adminId}/sales_invoices/${pdfId}/download_pdf.json`,
                     key: apiKey
                 })
             }
         )
         return await res.arrayBuffer();
    }

    async setActive(r, e){
        this.setState({loading:true})
        const pdf = await this.fetchPdf(r.id)
        this.setState({crrInvoice:pdf, crrRow: r, loading:false })
    }

    onDownloadMain(){
        const {crrInvoice, crrRow} = this.state;
        downloadPdf(crrInvoice, `${crrRow.invoice_id}.pdf`)
    }
    async onDownloadRow(r)
    {
       this.setState({loading:true})
       const pdf = await this.fetchPdf(r.id);
       this.setState({loading:false })
       downloadPdf(pdf, `${r.invoice_id}.pdf`)
    }
    clearActive(){
        this.setState({crrInvoice:null})
    }

    onDocumentLoadSuccess({ numPages }){
        this.setState({ numOfPages:numPages });
      }

   componentDidMount(){
        this.microsoftTeams = window? window.microsoftTeams: null;
        if(this.microsoftTeams){
            this.microsoftTeams.initialize();
            this.microsoftTeams.getContext(async ({entityId}) =>{
                const {apiKey, adminId, contact:{value :contactId}} = JSON.parse(entityId);
                const res = await fetch(
                    `https://moneybirdhandlerproxy.azurewebsites.net/api/MoneyBirdProxy`,
                    {
                        mode:"cors",
                        method:"POST",
                        body: JSON.stringify({
                            method:"GET",
                            path:`${adminId}/sales_invoices.json?filter=contact_id:${contactId}`,
                            key: apiKey
                        })
                    }
                )
                const json = await res.json();
                this.setState({
                    invoices: json,
                    adminId,
                    apiKey,
                    loading: false
                })
            })
        }
    }
    render(){
        const {crrInvoice, invoices, loading, numOfPages} = this.state;
        const columns =[
            {
                name:"Invoice id",
                selector:"invoice_id",
                sortable: true
            },
            {
                name:"Reference",
                selector:"reference",
                sortable: true
            },
            {
                name:"Invoice date",
                selector:"invoice_date",
                sortable: true,
                maxWidth:"15%",
                minWidth:"100px"
            },
            {
                name:"Amount",
                selector:"total_unpaid",
                sortable: true,
                maxWidth:"7%",
                minWidth:"85px",
                right:true
            },
            {
                name:"Status",
                selector:"state",
                sortable: true,
                width: "17%",
                cell: r=><div className={`state-label state-label--full-width ${r.state=="paid"? `state-label--success`: `state-label--warning`}`}><span className="state-label__text">{r.state}</span></div>
            },
            {
                name:"Download",
                maxWidth: "7%",
                minWidth: "80px",
                center:true,
                cell: r=><i onClick={()=>this.onDownloadRow(r)} className="fa fa-download download-btn" ></i>,
                ignoreRowClick: true,
            }
        ]
        return (
            
            <div >
                <Loader loading={loading}/>
                <header style={styles.mainSidePadding}>
                    {crrInvoice?
                        <div style={styles.mainHeader}>
                            <i className="left" onClick={this.clearActive} style={{...styles.arrow}}></i>
                            <i className="fa fa-download download-btn" onClick={this.onDownloadMain}></i>
                        </div>:null}
                </header>
                {
                    crrInvoice ? 
                    <div style={styles.mainPadding}>
                        <Document file={crrInvoice} onLoadSuccess={this.onDocumentLoadSuccess}>
                        {
                            Array.from(
                            new Array(numOfPages),
                            (el, index) => (
                                <Page
                                key={`page_${index + 1}`}
                                pageNumber={index + 1}
                                />
                            ),
                            )
                        }
                        </Document>
                    </div>
                    :
                    <div style={styles.mainPadding}>
                        <div style={styles.mainTable}>
                            <DataTable
                                columns={columns}
                                data={invoices? invoices:[]}
                                onRowClicked={this.setActive}
                                pointerOnHover={true}
                                highlightOnHover={true}
                                noHeader={true}
                            />
                        </div>
                    </div>
                }

            </div>
        )
    }
  
}

export default IndexPage;
